@import url('https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap');

.main {
    height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    color: white
}

.title {
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 50px;
    color: white
}

.content {
    font-family: 'Space Mono', monospace;
    font-weight: 400;
    font-size: 20px;
    padding: 0.5em;
    padding-top: 0;
    padding-bottom: 0;
    color: white
}
