button {
    background: none;
    border: 2px solid;
    font: inherit;
    line-height: 1;
    margin: 0.5em;
    padding: 1em 2em;
  }


.slide:hover{
    background: white;
    color: black;
    border: 2px white solid
}


.avatar {
    padding: 2em;
    vertical-align: middle;
    width: 250px;
    height: 250px;
    border-radius: 50%;
}

.outer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.outer .img {
    flex-grow: 1;
    flex-basis: calc(50% - 600px);
    margin: 50px;
    min-width: 280px;
}
.img img {
    display: block;
    max-width: 100%;
}
.txt {
    flex-grow: 1;
    flex-basis: calc(50% - 22px);
    margin: 50px;
    margin-top: 20px;
    min-width: 300px;
}

.aboutme{
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 30px;
    color: white;
}

.aboutmecontent{
    font-family: 'Space Mono', monospace;
    font-weight: 400;
    font-size: 18px;
    color: white;
    flex-grow: 1;
}

.selectedPublications{
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 30px;
    color: white;
    margin-bottom: 20px;
}

.teaching{
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 30px;
    color: white;
}

.teachinglist{
    font-family: 'Space Mono', monospace;
    font-weight: 400;
    font-size: 20px;
    margin: 20px;
    color: white
}
.outerCard {
    display: flex;
    flex-wrap: wrap;
}
.outerCard .imgCard {
    flex-grow: 1;
    flex-basis: calc(50% - 600px);
    margin: 10px;
    min-width: 280px;
}
.imgCard img {
    display: block;
    max-width: 100%;
}
.cardContent {
    flex-grow: 1;
    flex-basis: calc(50% - 22px);
    margin: 10px;
    min-width: 300px;
    font-family: 'Space Mono', monospace;
    font-weight: 400;
    font-size: 18px;
    color: white;
}

.aboutme{
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 30px;
    color: white;
}

.link{
    text-decoration: underline;
}

.connect{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    font-family: 'Space Mono', monospace;
    color: white;
    padding: 50px;
    padding-top: 20px;
}

.connectTitle{
    font-size: 33px;
    font-weight: 700;
    padding-bottom: 20px;
    color: yellow
}

.connectContent{
    font-size: 20px;
    font-weight: 400;
    text-align: center;
}

.educationTime{
    font-family: 'Space Mono', monospace;
    font-weight: 400;
    font-size: 16px;
    color: white;
    padding-top: 2px;
    min-width: 100px;
}

.educationCard{
    border: thin solid white;
    margin-top: 20px
}

.educationTitle{
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 22px;
    color: white;
}

.educationDegree{
    font-family: 'Space Mono', monospace;
    font-weight: 400;
    font-size: 16px;
    color: white;
}

@media (min-width: 768px) 
{
    .title {
        font-family: 'Space Mono', monospace;
        font-weight: 700;
        font-size: 90px;
    }
    
    .content {
        font-family: 'Space Mono', monospace;
        font-weight: 400;
        font-size: 20px;
    }

    .educationTime{
        font-family: 'Space Mono', monospace;
        font-weight: 400;
        font-size: 16px;
        color: white;
        margin-left: auto;
        margin-top: 4px;    
        text-align: right;
    }
}
